@media screen and (min-width: 48em) {
  .content__social-comment-icon {
    background-size: 2rem;
  }
}
@media screen and (min-width: 80em) {
  .content__social-comment-icon {
    background-size: 3rem;
  }
}
.content__social-comment-icon::after {
  content: "";
  position: absolute;
  right: 0;
  top: 100%;
  width: 0;
  height: 0;
  border: 0.5rem solid #007aff;
  border-bottom-color: transparent;
  border-right-color: transparent;
}
@media screen and (min-width: 48em) {
  .content__social-comment-icon::after {
    border-width: 0.75rem;
  }
}
@media screen and (min-width: 80em) {
  .content__social-comment-icon::after {
    border-width: 1rem;
  }
}

.content__social-comment-info {
  margin: 0;
  padding: 0.5rem;
}
@media screen and (min-width: 48em) {
  .content__social-comment-info {
    padding: 1rem;
  }
}
@media screen and (min-width: 80em) {
  .content__social-comment-info {
    grid-column: span 3;
    padding: 1.5rem 1.5rem 1.5rem 0;
  }
}

.content__social-comment-clickbait {
  color: #212121;
  display: block;
}

.home-salute {
  margin: 1rem 0;
}
@media screen and (min-width: 80em) {
  .home-salute {
    margin: 1.5rem 0;
  }
}

.home-salute__container {
  background: linear-gradient(to right, #cdcdcd, #b6b6b6);
  grid-area: content;
  display: flex;
  height: 7.5rem;
}
@media screen and (min-width: 48em) {
  .home-salute__container {
    height: 9.5rem;
  }
}
@media screen and (min-width: 80em) {
  .home-salute__container {
    height: 16rem;
  }
}

.home-salute__avatar {
  margin: auto;
  height: 5rem;
}
@media screen and (min-width: 48em) {
  .home-salute__avatar {
    height: 6rem;
  }
}
@media screen and (min-width: 80em) {
  .home-salute__avatar {
    height: 11.5rem;
  }
}